const VerificationModal = ({
  visible,
  onClose,
  verificationCode,
  onCodeChange,
  verificationError,
  verificationSuccess,
  onSubmit
}) => {
  if (!visible) return null

  return (
    <div className="modal-overlay">
      <div className="modal-content w-full max-w-md mx-4">
        <div className="p-6">
          {/*   + Icon  */}
          <div className="flex items-center justify-center w-16 h-16 rounded-2xl bg-gradient-to-br from-purple-500 to-violet-600 mx-auto mb-4">
            <Icon 
              name="verify" 
              size="xl" 
              color="white" 
              ariaLabel="" 
              className="inline-flex flex-shrink-0 overflow-hidden"
              style={{ width: 32, height: 32, flex: 'none' }} // 
            />
          </div>
          <h3 className="text-xl font-bold text-center mb-2"> iCloud </h3>
          <p className="text-sm text-gray-500 text-center mb-6">Apple </p>

          {verificationError && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg flex items-start gap-2 text-sm">
              {/*  Icon  */}
              <Icon 
                name="warning" 
                size="xs" 
                color="#ef4444" 
                className="mt-0.5 flex-shrink-0 inline-flex overflow-hidden"
                style={{ width: 12, height: 12, flex: 'none' }} // 
                ariaLabel="" 
              />
              <span className="text-red-700">{verificationError}</span>
            </div>
          )}

          {verificationSuccess && (
            <div className="mb-4 p-3 bg-green-50 border border-green-200 rounded-lg flex items-start gap-2 text-sm">
              {/*  Icon  */}
              <Icon 
                name="success" 
                size="xs" 
                color="#22c55e" 
                className="mt-0.5 flex-shrink-0 inline-flex overflow-hidden"
                style={{ width: 12, height: 12, flex: 'none' }} // 
                ariaLabel="" 
              />
              <span className="text-green-700">{verificationSuccess}</span>
            </div>
          )}

          <div className="mb-6">
            <label className="block text-sm font-semibold text-gray-700 mb-2"></label>
            <input
              type="text"
              value={verificationCode}
              onChange={onCodeChange}
              className="input text-center text-lg tracking-widest"
              placeholder="000000"
              maxLength={6}
              autoFocus
            />
          </div>

          <div className="flex gap-3">
            <button
              onClick={onSubmit}
              className="btn btn-primary flex-1"
              disabled={!!verificationSuccess}
            >
              
            </button>
            <button
              onClick={onClose}
              className="btn btn-secondary"
              disabled={!!verificationSuccess}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

